@use 'colors';

$contentWidth: 480px;
$contentHeight: 480px;

html {
    height: 100%;
}



body {
    background-color: colors.$backgroundSecondary;
    height: 100%;

    display: flex;

    flex-direction: column;

    align-items: center;
    justify-content: center;

    margin: 0;
    padding: 0;

    overflow: hidden;

    .content {
        display: flex;
        flex-direction: column;

        max-width: 100%;

        .yunglean-container {
            height: $contentHeight;

            width: $contentWidth;

            max-width: 100%;
            aspect-ratio: 1 / 1;

            & > video {
                height: $contentHeight;
                width: $contentWidth;
                max-width: 100%;
            }


        }

        .controls {
            width: 100%;
    
            display: flex;
    
            font-family: Arial, Helvetica, sans-serif;
            font-size: 30px;
            
            align-items: center;
            justify-content: space-around;

            & > div {
                width: calc(100% - 40px);
                padding: 30px 0;
                margin: 10px;
    
                text-align: center;
                vertical-align: middle;

                border-radius: 0.5em;
    
                background-color: colors.$foregroundSecondary;
            }
        }

    }
}

@media screen and (max-width: 480px) {    
    .content {
        height: 100%;
    }

    .controls {
        flex-direction: column;
    }

}




